// frontend/app/settings/page.tsx
// 功能: 后台设置页面
// 主要组件: SettingsPage

"use client";

import { useState, useEffect } from "react";
import { settingsAPI } from "@/lib/api";
import type { CreatorProfile } from "@/lib/api";

type Tab = "profiles" | "templates" | "channels" | "simulators" | "logs";

export default function SettingsPage() {
  const [activeTab, setActiveTab] = useState<Tab>("profiles");
  const [profiles, setProfiles] = useState<CreatorProfile[]>([]);
  const [templates, setTemplates] = useState<any[]>([]);
  const [channels, setChannels] = useState<any[]>([]);
  const [simulators, setSimulators] = useState<any[]>([]);
  const [logs, setLogs] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadData();
  }, [activeTab]);

  const loadData = async () => {
    setLoading(true);
    try {
      switch (activeTab) {
        case "profiles":
          setProfiles(await settingsAPI.listCreatorProfiles());
          break;
        case "templates":
          setTemplates(await settingsAPI.listFieldTemplates());
          break;
        case "channels":
          setChannels(await settingsAPI.listChannels());
          break;
        case "simulators":
          setSimulators(await settingsAPI.listSimulators());
          break;
        case "logs":
          setLogs(await settingsAPI.listLogs());
          break;
      }
    } catch (err) {
      console.error("加载数据失败:", err);
    } finally {
      setLoading(false);
    }
  };

  const tabs: { id: Tab; label: string }[] = [
    { id: "profiles", label: "创作者特质" },
    { id: "templates", label: "字段模板" },
    { id: "channels", label: "渠道管理" },
    { id: "simulators", label: "模拟器" },
    { id: "logs", label: "调试日志" },
  ];

  return (
    <div className="min-h-screen bg-surface-0">
      {/* 顶部导航 */}
      <header className="h-14 border-b border-surface-3 bg-surface-1 flex items-center justify-between px-6">
        <div className="flex items-center gap-4">
          <a
            href="/workspace"
            className="text-zinc-400 hover:text-zinc-200 transition-colors"
          >
            ← 返回工作台
          </a>
          <h1 className="text-lg font-semibold text-zinc-100">后台设置</h1>
        </div>
      </header>

      <div className="flex">
        {/* 侧边栏 */}
        <aside className="w-48 border-r border-surface-3 min-h-[calc(100vh-3.5rem)]">
          <nav className="p-4 space-y-1">
            {tabs.map((tab) => (
              <button
                key={tab.id}
                onClick={() => setActiveTab(tab.id)}
                className={`w-full px-3 py-2 text-left rounded-lg transition-colors ${
                  activeTab === tab.id
                    ? "bg-brand-600/20 text-brand-400"
                    : "text-zinc-400 hover:text-zinc-200 hover:bg-surface-3"
                }`}
              >
                {tab.label}
              </button>
            ))}
          </nav>
        </aside>

        {/* 内容区 */}
        <main className="flex-1 p-6">
          {loading ? (
            <div className="text-zinc-500">加载中...</div>
          ) : (
            <>
              {activeTab === "profiles" && (
                <ProfilesSection profiles={profiles} onRefresh={loadData} />
              )}
              {activeTab === "templates" && (
                <TemplatesSection templates={templates} />
              )}
              {activeTab === "channels" && (
                <ChannelsSection channels={channels} />
              )}
              {activeTab === "simulators" && (
                <SimulatorsSection simulators={simulators} />
              )}
              {activeTab === "logs" && (
                <LogsSection logs={logs} />
              )}
            </>
          )}
        </main>
      </div>
    </div>
  );
}

function ProfilesSection({
  profiles,
  onRefresh,
}: {
  profiles: CreatorProfile[];
  onRefresh: () => void;
}) {
  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-xl font-semibold text-zinc-100">创作者特质</h2>
        <button className="px-4 py-2 bg-brand-600 hover:bg-brand-700 rounded-lg transition-colors">
          + 新建特质
        </button>
      </div>

      <div className="grid gap-4">
        {profiles.map((profile) => (
          <div
            key={profile.id}
            className="p-4 bg-surface-2 border border-surface-3 rounded-xl"
          >
            <h3 className="font-medium text-zinc-200">{profile.name}</h3>
            <p className="text-sm text-zinc-500 mt-1">{profile.description}</p>
            {profile.traits && (
              <div className="mt-3 flex flex-wrap gap-2">
                {Object.entries(profile.traits).map(([key, value]) => (
                  <span
                    key={key}
                    className="px-2 py-1 text-xs bg-surface-3 rounded-full text-zinc-400"
                  >
                    {key}: {String(value)}
                  </span>
                ))}
              </div>
            )}
          </div>
        ))}
      </div>
    </div>
  );
}

function TemplatesSection({ templates }: { templates: any[] }) {
  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-xl font-semibold text-zinc-100">字段模板</h2>
        <button className="px-4 py-2 bg-brand-600 hover:bg-brand-700 rounded-lg transition-colors">
          + 新建模板
        </button>
      </div>

      <div className="grid gap-4">
        {templates.map((template) => (
          <div
            key={template.id}
            className="p-4 bg-surface-2 border border-surface-3 rounded-xl"
          >
            <div className="flex justify-between">
              <h3 className="font-medium text-zinc-200">{template.name}</h3>
              <span className="text-xs bg-surface-3 px-2 py-1 rounded-full text-zinc-400">
                {template.category}
              </span>
            </div>
            <p className="text-sm text-zinc-500 mt-1">{template.description}</p>
            <p className="text-xs text-zinc-600 mt-2">
              {template.fields?.length || 0} 个字段
            </p>
          </div>
        ))}
      </div>
    </div>
  );
}

function ChannelsSection({ channels }: { channels: any[] }) {
  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-xl font-semibold text-zinc-100">渠道管理</h2>
        <button className="px-4 py-2 bg-brand-600 hover:bg-brand-700 rounded-lg transition-colors">
          + 新建渠道
        </button>
      </div>

      <div className="grid gap-4 md:grid-cols-2">
        {channels.map((channel) => (
          <div
            key={channel.id}
            className="p-4 bg-surface-2 border border-surface-3 rounded-xl"
          >
            <h3 className="font-medium text-zinc-200">{channel.name}</h3>
            <p className="text-sm text-zinc-500 mt-1">{channel.description}</p>
            <span className="inline-block mt-2 text-xs bg-surface-3 px-2 py-1 rounded-full text-zinc-400">
              {channel.platform}
            </span>
          </div>
        ))}
      </div>
    </div>
  );
}

function SimulatorsSection({ simulators }: { simulators: any[] }) {
  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-xl font-semibold text-zinc-100">模拟器管理</h2>
        <button className="px-4 py-2 bg-brand-600 hover:bg-brand-700 rounded-lg transition-colors">
          + 新建模拟器
        </button>
      </div>

      <div className="grid gap-4">
        {simulators.map((simulator) => (
          <div
            key={simulator.id}
            className="p-4 bg-surface-2 border border-surface-3 rounded-xl"
          >
            <div className="flex justify-between">
              <h3 className="font-medium text-zinc-200">{simulator.name}</h3>
              <span className="text-xs bg-surface-3 px-2 py-1 rounded-full text-zinc-400">
                {simulator.interaction_type}
              </span>
            </div>
            <p className="text-sm text-zinc-500 mt-1">{simulator.description}</p>
          </div>
        ))}
      </div>
    </div>
  );
}

function LogsSection({ logs }: { logs: any[] }) {
  const handleExport = async () => {
    try {
      const data = await settingsAPI.exportLogs();
      const blob = new Blob([JSON.stringify(data, null, 2)], {
        type: "application/json",
      });
      const url = URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = url;
      a.download = `logs_${new Date().toISOString().split("T")[0]}.json`;
      a.click();
    } catch (err) {
      alert("导出失败");
    }
  };

  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-xl font-semibold text-zinc-100">调试日志</h2>
        <button
          onClick={handleExport}
          className="px-4 py-2 bg-surface-3 hover:bg-surface-4 rounded-lg transition-colors"
        >
          导出日志
        </button>
      </div>

      <div className="overflow-x-auto">
        <table className="w-full text-sm">
          <thead>
            <tr className="border-b border-surface-3">
              <th className="text-left py-2 px-3 text-zinc-500">时间</th>
              <th className="text-left py-2 px-3 text-zinc-500">阶段</th>
              <th className="text-left py-2 px-3 text-zinc-500">操作</th>
              <th className="text-left py-2 px-3 text-zinc-500">模型</th>
              <th className="text-right py-2 px-3 text-zinc-500">Tokens</th>
              <th className="text-right py-2 px-3 text-zinc-500">耗时</th>
              <th className="text-right py-2 px-3 text-zinc-500">成本</th>
            </tr>
          </thead>
          <tbody>
            {logs.map((log) => (
              <tr key={log.id} className="border-b border-surface-3/50">
                <td className="py-2 px-3 text-zinc-400">{log.created_at}</td>
                <td className="py-2 px-3 text-zinc-300">{log.phase}</td>
                <td className="py-2 px-3 text-zinc-300">{log.operation}</td>
                <td className="py-2 px-3 text-zinc-400">{log.model}</td>
                <td className="py-2 px-3 text-right text-zinc-400">
                  {log.tokens_in + log.tokens_out}
                </td>
                <td className="py-2 px-3 text-right text-zinc-400">
                  {log.duration_ms}ms
                </td>
                <td className="py-2 px-3 text-right text-green-400">
                  ${log.cost.toFixed(4)}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

